* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Work Sans", sans-serif;
}

body {
  background-color: #1a1b15;
}

.page__container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.picture__container {
  background-image: url("../../../build/img/1.webp");
  background-position: center;
  background-size: cover;
  opacity: 0.6;
  width: 100%;
  min-height: 50vh;
}

.title__header {
  font-weight: 900;
  color: #0da6f3;
  font-size: 3.3rem;
}

.header {
  text-align: center;
}

.nav__container {
  width: 100%;
  margin-top: 3rem;
}

.navigator {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  align-items: center;
}
.navigator .nav__item {
  text-decoration: none;
  font-size: 0.8rem;
  font-weight: 800;
  text-transform: uppercase;
  color: #1a1b15;
  background-color: #FFFFFF;
  padding: 1.1rem 0;
  width: 90%;
  border-radius: 5px;
}
.navigator .nav__item:hover {
  background-color: #0da6f3;
  color: #FFFFFF;
  transition: 0.3s;
}
.navigator .actual {
  background-color: #0da6f3;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .page__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 900px;
  }
  .picture__container {
    min-height: 100vh;
  }
  .navigator {
    display: flex;
    flex-direction: row;
    column-gap: 1rem;
    justify-content: center;
  }
  .nav__item {
    max-width: 30%;
  }
}
.app {
  width: 95%;
  margin: 0 auto;
  padding-top: 5rem/2;
}

.section {
  display: none;
  margin-top: 2rem;
}

/* Seccion 1 : Servicios */
.title__section {
  font-size: 3rem;
  font-weight: 900;
  color: #0da6f3;
  text-align: center;
}

.section__p {
  text-align: center;
  color: #FFFFFF;
  font-size: 1.3rem;
  letter-spacing: 3px;
  margin-top: 2rem;
}

.mostrar-seccion {
  display: block !important;
}

.listado-servicios {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 2rem;
  margin-top: 2rem;
}

.div__servicios {
  background-color: #FFFFFF;
  border-radius: 1rem;
  padding: 1.9rem 1rem;
  margin: 0.5rem 0;
  display: flex;
  flex-direction: column;
  row-gap: 0.5rem;
}
.div__servicios:hover {
  filter: brightness(0.9);
  cursor: pointer;
}

.nom__servicios {
  font-size: 1.8rem;
  letter-spacing: 1px;
}

.precio__servicio {
  font-size: 2.2rem;
  font-weight: 800;
  color: #0da6f3;
}

.seleccionado {
  background-color: #0da6f3;
}
.seleccionado p {
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    margin-top: 2rem;
  }
}
.paginacion {
  display: flex;
  justify-content: space-between;
  column-gap: 1rem;
  margin: 2rem 0;
}
.paginacion button {
  padding: 1rem 2.3rem;
  border-style: none;
  border-radius: 0.2rem;
  color: #FFFFFF;
  background-color: #0da6f3;
  font-size: 1.3rem;
  font-weight: 900;
}
.paginacion button.ocultar {
  visibility: hidden;
}
.paginacion button:hover {
  cursor: pointer;
  filter: brightness(0.7);
}

.form {
  margin: 3rem 0 10rem 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}

.campo {
  display: flex;
  align-items: center;
}
.campo label {
  color: #FFFFFF;
  font-size: 1.2rem;
  flex: 0 0 10rem;
}

input {
  padding: 1rem 1rem;
  width: 100%;
  border-radius: 0.7rem;
  outline-style: none;
  font-size: 1.1rem;
  font-weight: 700;
  color: #1a1b15;
}

.alerta {
  padding: 0.7rem;
  text-align: center;
  font-size: 1rem;
  letter-spacing: 3px;
  text-transform: uppercase;
  border-radius: 5px;
}
.alerta.error {
  background-color: rgb(194, 0, 0);
  color: #FFFFFF;
}

.invalidar__cita {
  text-align: center;
  color: #FFFFFF;
  font-size: 1.3rem;
  letter-spacing: 3px;
  margin-top: 2rem;
  text-transform: uppercase;
}

.span__resumen {
  font-weight: 900;
  font-size: 1.5rem;
  color: #0da6f3;
}

.resumenCita {
  margin: 1rem 0;
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: 700;
  letter-spacing: 1px;
}

.resumen-servicios {
  margin-top: 2.5rem;
}
.resumen-servicios h3 {
  font-size: 2.5rem;
}

.contenedor-servicio {
  margin: 2rem 0;
  border-bottom: 1px solid #43453c;
  display: grid;
}
.contenedor-servicio .servicio {
  margin: 1rem 0;
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: 700;
  letter-spacing: 1px;
}
.contenedor-servicio .precio {
  margin: 1rem 0;
  color: #0da6f3;
  font-size: 2rem;
  font-weight: 700;
  letter-spacing: 1px;
}

.total__pagar {
  font-size: 2.5rem;
  font-weight: 900;
  color: #FFFFFF;
  margin: 5rem 0;
}

.span__total {
  font-weight: 900;
  font-size: 2.5rem;
  color: #0da6f3;
}